  return (
    <div className="blog-detail">
      <div className="content-wrapper container">
        <Grid item md={12}>
          <BreadCrumbs name={layoutData?.name} heading={layoutData?.heading} />
        </Grid>
        <Grid container spacing={2}>
          <Grid item md={9}>
            <Header headerData={layoutData} />
          </Grid>
          <Grid item md={3}></Grid>
        </Grid>
      </div>

      {children}

      <div className="card-wrapper container">
        <Card cardData={cardData.filter((blog, i) => i < 3)} col={4} gap={4} />
        <div className="btn-wrapper">
          <Button text="View All Blogs" variant="fill" href="/blog" />
        </div>
      </div>
    </div>
  )
