import * as React from "react"
import Breadcrumbs from "@mui/material/Breadcrumbs"
import Typography from "@mui/material/Typography"
import Link from "@mui/material/Link"
import Stack from "@mui/material/Stack"
import VisibilitySensor from "react-visibility-sensor"
import "./Detail.scss"

function Icon() {
  return (
    <svg
      width="8"
      height="10"
      viewBox="0 0 8 10"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M0.24 0.24H3.12L7.72 4.78L3.12 9.32H0.24L4.86 4.78L0.24 0.24Z"
        fill="#999999"
      />
    </svg>
  )
}

const handleVisibility = visible => {
  const element = document.querySelector(".table-of-content")
  if (visible) {
    element.style.top = "190px"
    element.style.maxHeight = "650px"
    element.style.display = "none"
    return
  }
  element.style.top = "70px"
  element.style.maxHeight = "800px"
}

export default function CustomSeparator({ name, heading }) {
  const breadcrumbs = [
    <Link underline="hover" key="1" color="inherit" href="/">
      Home
    </Link>,
    <Link underline="hover" key="2" color="inherit" href="/blog">
      Blog
    </Link>,
    <Link underline="hover" key="2" color="inherit" href="/blog">
      {name}
    </Link>,
    <Typography key="3" color="text.primary">
      {heading?.substring(0, 60)}
    </Typography>,
  ]

  return (
    <Stack spacing={2}>
      <VisibilitySensor
        onChange={handleVisibility}
        delayedCall
        scrollCheck
        intervalDelay={10}
      >
        <Breadcrumbs
          separator={<Icon />}
          aria-label="breadcrumb"
          className="breadcrumbs breadcrumbs-visibility"
        >
          {breadcrumbs}
        </Breadcrumbs>
      </VisibilitySensor>
    </Stack>
  )
}
